@import url('https://fonts.googleapis.com/css2?family=IM+Fell+English+SC&display=swap');

/* ─────────────────────────────────────────
   배경 지지직 효과
   ───────────────────────────────────────── */
.glitch-background {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: repeating-linear-gradient(0deg,
            #000,
            #000 2px,
            #111 2px,
            #111 4px);
    animation: backgroundNoise 0.25s infinite;
    position: relative;
    cursor: pointer;
    padding: 0 4vw;
    /* 양 옆 아주 살짝 여유 */
}

@keyframes backgroundNoise {
    0% {
        background-position: 0 0;
    }

    50% {
        background-position: 5px 10px;
    }

    100% {
        background-position: -5px -10px;
    }
}

/* ─────────────────────────────────────────
   시작 전 오버레이
   ───────────────────────────────────────── */
.start-overlay {
    position: absolute;
    inset: 0;
    background-color: rgba(5, 5, 5, 0.85);
    display: flex;
    justify-content: center;
    align-items: center;
}

.start-text {
    color: #ccc;
    font-family: 'IM Fell English SC', serif;
    /* 줄바꿈 없이 크기만 유동 조절 */
    font-size: clamp(14px, 2.5vw, 22px);
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    max-width: 92vw;
    text-shadow: 0 0 8px rgba(255, 255, 255, 0.2);
    animation: blinkText 1.5s infinite;
}

@keyframes blinkText {

    0%,
    100% {
        opacity: 1;
    }

    50% {
        opacity: 0.3;
    }
}

/* ─────────────────────────────────────────
   본문 텍스트(타이핑)
   ───────────────────────────────────────── */
.creepy-text {
    font-family: 'IM Fell English SC', serif;
    /* 줄바꿈 없이 크기만 유동 조절 */
    font-size: clamp(20px, 4vw, 38px);
    white-space: nowrap;
    text-align: center;
    text-overflow: ellipsis;
    overflow: hidden;
    max-width: 92vw;
    color: #e1dcdc;
    text-shadow:
        0 0 4px #aa0000,
        0 0 8px #440000;
}

/* 커서 */
.typing-text::after {
    content: '|';
    /* 글자 크기와 함께 자연스럽게 스케일되도록 inherit */
    font-size: inherit;
    line-height: 1;
    animation: blinkCursor 1.5s infinite;
    color: #ff4444;
    margin-left: 4px;
}

@keyframes blinkCursor {

    0%,
    100% {
        opacity: 1;
    }

    50% {
        opacity: 0;
    }
}

/* 노이즈 글자(타이핑 중 섞여 나오는 랜덤 문자) */
.noise-char {
    color: #a94444;
    font-weight: bold;
    font-size: inherit;
    /* 본문 글자 크기에 동기화 */
    animation: glitchJitter 0.4s infinite;
}

@keyframes glitchJitter {
    0% {
        transform: translateX(1px);
        opacity: 0.8;
    }

    50% {
        transform: translateX(-1px);
        opacity: 0.4;
    }

    100% {
        transform: translateX(0);
        opacity: 0.8;
    }
}

/* ─────────────────────────────────────────
   접근성: 모션 축소 환경에서 애니메이션 약화
   ───────────────────────────────────────── */
@media (prefers-reduced-motion: reduce) {
    .glitch-background {
        animation: none;
    }

    .typing-text::after {
        animation: none;
    }

    .start-text {
        animation: none;
    }
}